import { connect } from 'react-redux'
import {
  shouldDisableConfirmNavigation,
  submitConfirmNavigationName,
  leaveConfirmNavigationName,
  isJobOfferFormConfirmNavigation,
  confirmNavigationModalTitle
} from 'Shared/selectors/reduxForm'
import ConfirmPrompt from 'Shared/components/ConfirmNavigation/ConfirmPrompt'
import { handleSaveConfirmNavigation } from 'Shared/ducks/user/userConfirmNavigation'

const mapStateToProps = (state, ownProps) => ({
  disabled: shouldDisableConfirmNavigation(state),
  submitName: submitConfirmNavigationName(state),
  cancelName: leaveConfirmNavigationName(state),
  title: confirmNavigationModalTitle(state),
  isJobOfferConfirm: isJobOfferFormConfirmNavigation(state)
})

const mapDispatchToProps = (dispatch, ownProps) => ({
  handleSave: () => dispatch(handleSaveConfirmNavigation())
})

export default connect(mapStateToProps, mapDispatchToProps)(ConfirmPrompt)



