import React, { Component } from 'react'
import ConfirmPrompt from 'Shared/connectors/Components/ConfirmPromptConnector'

class ConfirmNavigation extends Component {
  state = {
    show: true
  }

  componentWillReceiveProps () {
    this.setState({ show: true })
  }

  close = (redirect) => {
    this.hidePrompt()
    this.props.callback(redirect)
  }

  hidePrompt = () => this.setState({ show: false })

  render () {
    const { message } = this.props
    return (
      <ConfirmPrompt
        message={message}
        close={this.close}
        show={this.state.show}
        handleClose={this.hidePrompt}
      />
    )
  }
}

ConfirmNavigation.propTypes = {
}

export default ConfirmNavigation



