import { getFormsDirtyState, getCompleteProfileForms, getJobOfferForms } from 'Shared/selectors/reduxForm'
import { getJobOfferStatus } from 'Shared/selectors/jobOffers'
import { submit, getFormValues } from 'redux-form'
import { isNotEmpty } from 'Shared/helpers/Ramda'
import { editJobOffer, setNewApplicationNotifications } from 'Shared/ducks/jobOffers'
import { setTeamMembersForJobOffer } from 'Shared/ducks/jobOffersMembers'
import { isAdminEmployer } from 'Shared/selectors/user'
import { getEmployerCompanyDetailsCompletion } from 'Shared/selectors/organisations'
import { updateOrganisation } from 'Shared/ducks/organisations'
import { updateUserAttributes } from 'Chat/ducks/classes/users'
import { getIdFromLocation } from 'Shared/helpers/string'
import { Statuses } from 'Shared/constants/Job'
import { profileSteps } from 'Shared/ducks/user/candidate'
import * as R from 'ramda'

export const saveDirtyProfileCompletionForms = () => (dispatch, getState) => {
  const dirtyForms = getFormsDirtyState(getState())
  return Promise.all(dirtyForms.map(form => dispatch(submit(form))))
}

export const saveJobOffer = values => async dispatch => {
  await dispatch(editJobOffer({...values}))
  await dispatch(
    setTeamMembersForJobOffer(
      values.id,
      R.pipe(
        R.propOr([], 'teamMembers'),
        R.map(
          R.pipe(
            R.prop('value'),
            R.objOf('id')
          )
        )
      )(values)
    )
  )
  return dispatch(setNewApplicationNotifications(values.id, values.shouldNotifyOnNewApplication))
}

export const handleSaveConfirmNavigation = () => async (dispatch, getState) => {
  const completeProfileForms = getCompleteProfileForms(getState())
  const jobOfferForms = getJobOfferForms(getState())
  const getJobOfferInitStatus = getJobOfferStatus(getState(), getIdFromLocation(window.location.pathname))

  switch (true) {
    case isNotEmpty(completeProfileForms):
      await dispatch(saveDirtyProfileCompletionForms())
      return dispatch(profileSteps())
    case isNotEmpty(jobOfferForms) && jobOfferForms.length === 1:
      const formValues = getFormValues(jobOfferForms[0])(getState())
      if (isAdminEmployer(getState()) && !getEmployerCompanyDetailsCompletion(getState())) {
        await dispatch(updateOrganisation(formValues.companyInformation, true))
        dispatch(updateUserAttributes())
      }

      const status = getJobOfferInitStatus === Statuses.draft ? Statuses.draft : Statuses.active

      return dispatch(saveJobOffer(R.dissoc('companyInformation', { ...formValues, status })))
    default:
      return null
  }
}



