import React from 'react'
import PropTypes from 'prop-types'
import Modal from 'Shared/components/Modal'
import withTranslations from 'Shared/hoc/withTranslations'

const ConfirmPrompt = (props) => {
  const onLeaveAndSubmit = async () => {
    if (props.isJobOfferConfirm && props.disabled) {
      return props.handleClose()
    }
    await props.handleSave()
    return props.close(true)
  }

  const onLeave = () => props.close(true)

  const { message } = props
  const splitMessage = message.split('\n')
  return (
    <Modal
      title={props.title}
      handleSubmit={onLeaveAndSubmit}
      handleClose={props.handleClose}
      show={props.show}
      handleCancel={onLeave}
      cancelText={props.cancelName}
      submitText={props.submitName}
      showContact={false}
      disableSubmitButton={!props.isJobOfferConfirm && props.disabled}
      showCancel
    >
      <p>{splitMessage.length === 2 ? splitMessage[0] : message}</p>
      <p>{splitMessage.length === 2 && splitMessage[1]}</p>
    </Modal>
  )
}

ConfirmPrompt.propTypes = {
  show: PropTypes.bool.isRequired,
  close: PropTypes.func.isRequired
}

export default withTranslations(ConfirmPrompt)



